.Header {
    z-index: 1;
    display: grid;
    grid-template-columns: 15% 60% 15%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    grid-gap: 10px;
    padding: 20px;
    background:#F5C852;
    background-image: -webkit-gradient(linear, left top, right top, from(#e0a91c),color-stop(#f5c852),to(#e0a91c));
    background-image: -webkit-linear-gradient(left, #e0a91c,#f5c852,#e0a91c);
    background-image: -o-linear-gradient(left, #e0a91c,#f5c852,#e0a91c);
    background-image: linear-gradient(to right, #e0a91c,#f5c852,#e0a91c);
    -webkit-box-shadow: 0 3px 5px rgb(22, 22, 22);
            box-shadow: 0 3px 5px rgb(22, 22, 22);
}


